"use client";

import { useState } from "react";
import styles from "./CreateEvent.module.css";

import Stepper from "./components/Stepper";
import OrganizerDetails from "./steps/OrganizerDetails";
import EventDetails from "./steps/EventDetails";
import MediaTickets from "./steps/MediaTickets";
import toast from "react-hot-toast";

import {
  createEventStep1Schema,
  createEventStep2Schema,
  createEventStep3Schema,
} from "../../../../components/validation";

import { createEventApi } from "../../../../lib/api/event.api";
import { getUserData } from "../../../../lib/auth";

/* ================= INITIAL STATE ================= */

const INITIAL_FORM_DATA = {
  organizer: {
    organizations: [
      {
        hostBy: "",
        orgName: "",
        location: "",
        organizerName: "",
        organizerNumber: "",
        department: "",
      },
    ],
  },
  event: {
    title: "",
    category: "",
    eventType: "",
    tags: [],
    about: "",
    mode: "online",
    calendar: [],
    country: "",
    state: "",
    city: "",
    mapLink: "",
  },
  media: {
    bannerImages: [],
    perks: "",
    certification: "",
    accommodation: "",
    tickets: [],
    paymentLink: "",
    whatsapp: "",
    instagram: "",
    linkedin: "",
    website: "",
  },
};

/* ================= PAYLOAD BUILDER ================= */

const buildCreateEventPayload = (formData, user) => {
  const organizer = formData.organizer.organizations[0];
  const event = formData.event;
  const media = formData.media;

  return {
    createdBy: user?.id || user?.identity,

    title: event.title,
    description: event.about,
    mode: event.mode?.toUpperCase(),

    categoryIdentity: event.category,
    eventTypeIdentity: event.eventType,

    eligibleDeptIdentities: organizer.department
      ? [organizer.department]
      : [],

    tags: event.tags || [],

    paymentLink: media.paymentLink,
    eventLink: media.website,

    socialLinks: {
      whatsapp: media.whatsapp,
      instagram: media.instagram,
      linkedIn: media.linkedin,
    },

    location:
      event.mode !== "online"
        ? {
            country: event.country,
            state: event.state,
            city: event.city,
            mapLink: event.mapLink,
          }
        : null,

    collaborators: [
      {
        organizationName: organizer.orgName,
        organizerName: organizer.organizerName,
        organizerNumber: organizer.organizerNumber,
        location: organizer.location,
        role: "primary_host",
      },
    ],

    calendars: (event.calendar || []).map((c) => ({
      timeZone: c.timeZone || "Asia/Kolkata",
      startDate: c.startDate,
      endDate: c.endDate,
      startTime: c.startTime,
      endTime: c.endTime,
    })),

    tickets: (media.tickets || []).map((t) => ({
      name: t.name,
      description: t.description,
      sellingFrom: t.from,
      sellingTo: t.to,
      isPaid: t.type === "PAID",
      price: t.type === "PAID" ? Number(t.amount) : 0,
      totalQuantity: Number(t.total),
    })),

    perkIdentities: media.perks ? [media.perks] : [],
    certIdentities: media.certification ? [media.certification] : [],
    accommodationIdentities: media.accommodation
      ? [media.accommodation]
      : [],
  };
};

export default function CreateEvent() {
  const [step, setStep] = useState(1);
  const [formData, setFormData] = useState(INITIAL_FORM_DATA);

  /* ================= STEP 1 ================= */
  const handleStep1Next = async () => {
    try {
      await createEventStep1Schema.validate(formData.organizer, {
        abortEarly: false,
      });
      setStep(2);
    } catch (err) {
      toast.error(err?.errors?.[0] || "Please fill all organizer details");
    }
  };

  /* ================= STEP 2 ================= */
  const handleStep2Next = async () => {
    try {
      await createEventStep2Schema.validate(formData.event, {
        abortEarly: false,
      });
      setStep(3);
    } catch (err) {
      toast.error(err?.errors?.[0] || "Please fill all event details");
    }
  };

  /* ================= FINAL SUBMIT ================= */
  const handleFinalSubmit = async () => {
    try {
      await createEventStep3Schema.validate(formData.media, {
        abortEarly: false,
      });

      const user = getUserData();
      const orgId = user?.identity;

      if (!orgId) {
        toast.error("Organization not found");
        return;
      }

      /* ===== BUILD FINAL PAYLOAD ===== */
      const payload = buildCreateEventPayload(formData, user);

      /* ===== CONSOLE CHECK (EXACT STRUCTURE) ===== */
      console.log("===== CREATE EVENT PAYLOAD =====");
      console.log(JSON.stringify(payload, null, 2));

      /* ===== FORMDATA (JSON + FILES) ===== */
      const fd = new FormData();
      fd.append("data", JSON.stringify(payload));

      if (formData.media.bannerImages?.length) {
        formData.media.bannerImages.forEach((file) => {
          fd.append("bannerImages", file);
        });
      }

      /* ===== API CALL ===== */
      const res = await createEventApi(orgId, fd);

      if (res?.success) {
        toast.success("Event created successfully");
        setFormData(INITIAL_FORM_DATA);
        setStep(1);
      } else {
        toast.error(res?.message || "Event creation failed");
      }
    } catch (err) {
      toast.error(
        err?.errors?.[0] ||
          err?.message ||
          "Please complete media & ticket details"
      );
    }
  };

  return (
    <div className={styles.wrapper}>
      <Stepper step={step} />

      {step === 1 && (
        <OrganizerDetails
          data={formData.organizer}
          setData={(d) => setFormData({ ...formData, organizer: d })}
          onNext={handleStep1Next}
        />
      )}

      {step === 2 && (
        <EventDetails
          data={formData.event}
          setData={(d) => setFormData({ ...formData, event: d })}
          onBack={() => setStep(1)}
          onNext={handleStep2Next}
        />
      )}

      {step === 3 && (
        <MediaTickets
          data={formData.media}
          setData={(d) => setFormData({ ...formData, media: d })}
          onBack={() => setStep(2)}
          onSubmit={handleFinalSubmit}
        />
      )}
    </div>
  );
}
